import React, { Component } from 'react';

//import { Spinner } from '../Spinner';

//import { Timeline } from 'react-twitter-widgets';
//import { FBPage } from 'facebook-plugins';
import './Artist.css';

class Artist extends Component {
  constructor() {
    super();

    this.state = {
      data: null,
      loadingData: true,
      twitter: null
    };
  }

  componentDidMount() {
    const { name, id } = this.props.match.params;

    this.getData(id);
  }

  getData(id) {
    const API = `https://itunes.apple.com/lookup?id=${id}`;

    fetch(API)
      .then(res => res.json())
      .then(data => {
        this.setState({ data: data.results[0], loadingData: false });
        this.twitter();
      });
  }

  twitter() {
    const twitter = (
      <div className="twitter">
        {/*<Timeline
          dataSource={{
            sourceType: 'profile',
            screenName: this.state.data.artistName
          }}
          options={{
            username: this.state.data.artistName,
            height: '500'
          }}
        />*/}
      </div>
    );

    this.setState({ twitter: twitter });
  }

  render() {
    return(<h1>Artist Page<br/>Soon!</h1>);
    /*if (!this.state.loadingData) {
      return (
        <div>
          <div className="container">
            <h2>{this.state.data.artistName}</h2>
            <h3>About</h3>

            <div className="album-page">
              <section className="secondary">

              </section>

              <section className="primary">

              </section>
            </div>

            <h3>Songs</h3>

            <h3>Albums</h3>

            <h3>Videos</h3>
          </div>

          <div className="network-container">
            <div className="container">
              {this.state.twitter}
            </div>
          </div>
        </div>
      );
    } else {
      return <Spinner/>;
    }*/
  }
}

export default Artist;



