import React, { Component } from 'react';
import Delay from 'react-delay';
import { Link } from 'react-router-dom';

import { Spinner } from '../Spinner';

class Single extends Component {
  constructor() {
    super();

    this.state = {
      data: null,
      lyrics: null,
      loadingData: true,
      loadingLyrics: true
    };
  }

  componentDidMount() {
    const { song, id } = this.props.match.params;

    this.getData(id);
  }

  getData(id) {
    const API = `https://itunes.apple.com/lookup?id=${id}`;

    fetch(API)
      .then(res => res.json())
      .then(data => {
        this.setState({ data: data.results[0], loadingData: false });
        this.lyrics();
      });
  }

  lyrics() {
    const API = 'https://api.lyrics.ovh/v1/';

    fetch(`${API + this.state.data.artistName}/${this.state.data.trackName}`)
      .then(res => res.json())
      .then(data => {
        data.lyrics = data.lyrics.split('\n').map(item => <span>{item}<br/></span>);
        this.setState({ lyrics: data.lyrics, loadingLyrics: false });
      })
      .catch(err => this.setState({ lyrics: null, loadingLyrics: false }));
  }

  render() {
    if (!this.state.loadingData) {
      let album = this.state.data.collectionName.toLowerCase().replace(/ /g, '+');
      let artist = this.state.data.artistName.toLowerCase().replace(/ /g, '+');

      let released = new Date(this.state.data.releaseDate);

      let year = released.getFullYear();

      let s = Math.floor(this.state.data.trackTimeMillis / 1000);
      let m = Math.floor(s / 60);
      //let h = Math.floor(m / 60);
      return (
        <div className="container">
          <div className="page">
            <section className="primary">
              <div>
                <Delay wait={100}>
                  <img src={this.state.data.artworkUrl100} alt={this.state.data.trackName}/>
                </Delay>

                <Delay wait={400}>
                  <div className="info">
                    <p>Single Price: {this.state.data.trackPrice}$</p>
                    <p>Length: {`${m % 60}:${s % 60}`}</p>
                    <audio controls className="player" preload="false">
                      <source src={this.state.data.previewUrl}/>
                    </audio>
                  </div>
                </Delay>
              </div>
            </section>

            <section className="secondary">
              <h2>{this.state.data.trackName}<span className={this.state.data.trackExplicitness}></span></h2>
              From: &nbsp;
              <Link to={`/album/${album}/${this.state.data.collectionId}`}>
                <span>{this.state.data.collectionName}</span>
              </Link>
              <Link to={`/artist/${artist}/${this.state.data.artistId}`}>
                <h3>{this.state.data.artistName}</h3>
              </Link>
              <p>{`${this.state.data.primaryGenreName}  ${year}`}</p>
              { !this.state.loadingLyrics ? this.state.lyrics : <Spinner/> }
            </section>
          </div>
        </div>
      );
    } else {
      return <Spinner/>;
    }
  }
}

export default Single;



