import React from 'react';
import { Route, Redirect } from 'react-router-dom';

import { Home } from './Home';
import Search from './Search';
import Album from './Album';
import Artist from './Artist';
import Single from './Single';
import { About } from './About';

export const Content = ({value}) => {
  let query;

  if (value !== '' && window.location.pathname !== '') {
    query = (
      <Redirect to={`/search/${value}`}/>
    );
  } else if (value === '' && window.location.pathname === '') {
    query = (
      <Redirect to={`/`}/>
    );
  }

  return (
    <div>
      {query}
      <Route exact path="/" component={Home}/>
      <Route path="/search/:query" component={Search}/>
      <Route exact path="/album" render={() => <Redirect to="/"/>}/>
      <Route path="/album/:track/:id" component={Album}/>
      <Route exact path="/music-video" render={() => <Redirect to="/"/>}/>
      <Route path="/music-video/:artist/:title" component={Album}/>
      <Route exact path="/artist" render={() => <Redirect to="/"/>}/>
      <Route path="/artist/:name/:id" component={Artist}/>
      <Route exact path="/single" render={() => <Redirect to="/"/>}/>
      <Route path="/single/:song/:id" component={Single}/>
      <Route path="/about" component={About}/>
    </div>
  );
}



