import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';

import './App.css';
import Menu from './Menu';
import { Content } from './Content';

/*import { connect } from "react-redux";
import { search } from "./actions";

const mapStateToProps = state => {
  return {
    data: state.data
  }
};
const mapDispatchToProps = { search };*/

class App extends Component {
  constructor() {
    super();

    this.state = {
      searchValue: '',
      darkMode: null
    };
  }

  componentDidMount = () => this.setState({ darkMode: false });

  getValue = val => this.setState({ searchValue: val });

  render() {
    return (
      <BrowserRouter basename={'/Muzika'}>
        <div className={ `App ${ this.state.darkMode ? 'dark' : 'light' }` }>
          <Menu dataSearch={this.getValue}/>
          <div className="wrap">
            <Content value={this.state.searchValue}/>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export const AppContainer = App;

