import React, { Component } from 'react';

import SearchForm from '../SearchForm';
import './Menu.css';

export default class Menu extends Component {
  getValue = val => this.props.dataSearch(val);

  render() {
    return (
      <nav>
        <SearchForm dataSearch={this.getValue}/>
      </nav>
    );
  }
}



