import React from 'react';
import { Link } from 'react-router-dom';
import Delay from 'react-delay';

import './Cards.css';

export const Cards = ({val, index}) => {
  const artist = val.artistName.toLowerCase().replace(/ /g, '+');
  const song = val.trackName.toLowerCase().replace(/ /g, '+');

  let album;
  if (val.collectionName !== undefined) {
    album = val.collectionName.toLowerCase().replace(/ /g, '+');
  }

  return (
    <Delay wait={50 * index}>
      <div className="card">
        <div className="artwork">
          <img src={val.artworkUrl100} alt={val.trackName}/>
        </div>

        <div className="content">
          <Link to={`/single/${song}/${val.trackId}`}>
            <h3 className="song-title">{val.trackName}</h3>
          </Link>

          <Link to={`/album/${album}/${val.collectionId}`}>
            <p className="album">{val.collectionName}</p>
          </Link>

          {/*<audio controls className="player" preload="false">
            <source src={val.previewUrl}/>
          </audio>*/}

          <Link to={`/artist/${artist}/${val.artistId}`}>
            <p className="author">{val.artistName}</p>
          </Link>

          <div className={val.trackExplicitness}></div>
        </div>
      </div>
    </Delay>
  );
}



