import React, { Component } from 'react';
//import { Link } from 'react-router-dom';

//import { Spinner } from '../Spinner';

class Album extends Component {
  constructor() {
    super();

    this.state = {
      data: null,
      loadingData: true,
      track: null
    };
  }

  componentDidMount() {
    const { track, id } = this.props.match.params;

    this.getData(id);
  }

  getData(id) {
    const API = `https://api.spotify.com/v1/`;

    fetch(API + 'albums/4aawyAB9vmqN3uQ7FjRGTy/')
      .then(res => res.json())
      .then(data => {
        this.setState({ data: data.results[0], loadingData: false });
      });
  }

  render() {
    return(<h1>Album Page<br/>Soon!</h1>);


    /*if (!this.state.loadingData) {
      let artist = this.state.data.artistName.toLowerCase().replace(/ /g, '+');
      return (
        <div className="container">
          <div className="page">
            <section className="primary">
              <img src={this.state.data.artworkUrl100} alt={this.state.data.trackName}/>
            </section>

            <section className="secondary">
              <h2>{this.state.data.collectionName}</h2>
              <Link to={`/artist/${artist}/${this.state.data.collectionId}`}>
                <h3>{this.state.data.artistName}</h3>
              </Link>
            </section>
          </div>
        </div>
      );
    } else {
      return <Spinner/>;
    }*/
  }
}

export default Album;



