import React, { Component } from 'react';
import { Cards } from '../Cards';
import { Spinner } from '../Spinner';

class Search extends Component {
  constructor() {
    super();

    this.state = {
      data: null,
      loading: true
    };
  }

  componentDidMount = () => this.getData(this.props.match.params.query);

  getSnapshotBeforeUpdate = val => this.getData(val.match.params.query);

  getData(val) {
    const API = 'https://itunes.apple.com/search?term=';

    fetch(`${API + val}&entity=song,musicVideo`)
      .then(res => res.json())
      .then(data => this.setState({ data: data.results, loading: false }));
  }

  render() {
    let data;
    if (this.state.data !== null && this.state.data.length > 0) {
      data = this.state.data.map((val, index) => <Cards val={val} index={index}/>);
    }

    return <div className="list">{ !this.state.loading ? data : <Spinner/> }</div>;
  }
}

export default Search;



