import { SEARCH_VALUE } from "../constants/action-types";

const initialState = {
  searchValue: null
};

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case SEARCH_VALUE:
      state.searchValue.push(action);
      return { ...state, searchValue: state.articles.concat(action.payload) };
    default:
      return state;
  }
};

export default rootReducer;



